import React, { useState } from 'react';
import { Box } from '@mui/material';
import {
	YMaps,
	Map,
	Placemark,
	ZoomControl,
	FullscreenControl,
	SearchControl,
} from '@pbe/react-yandex-maps';

const MapPage = () => {
	const [X, setX] = useState(0);
	const [Y, setY] = useState(0);
	const [capacity, setCapacity] = useState();

	const setCoords = (x, y) => {
		setX(x);
		setY(y);
	};

	const defaultState = {
		center: [59.9397392, 30.3140793],
		zoom: 11,
		controls: [],
	};

	const mapClickHandler = async e => {
		const [x, y] = e.get('coords');
		const result = await fetch(
			`http://localhost:8000/api/get_capacity?${new URLSearchParams({
				x,
				y,
			})}`,
		).then(res => res.json());
		console.log(result);
		setCoords(x, y);
		setCapacity(
			'capacity: ' +
				Math.floor(result.capacity.coefficient * 100) +
				'% ' +
				result.build.data.short_address,
		);
	};

	return (
		<Box
			id="map"
			display="flex"
			flexDirection="column"
			justifyContent="center"
			alignItems="center"
			sx={{ height: '100%', width: '100%' }}>
			<YMaps
				query={{
					lang: 'en_RU',
					apikey: 'b4e76738-74b1-4875-9ff9-782c376e12a0',
				}}>
				<Map
					defaultState={defaultState}
					height={'90%'}
					width={'90%'}
					onClick={mapClickHandler}>
					<Placemark
						geometry={[X, Y]}
						properties={{ iconCaption: capacity }}
					/>
					<ZoomControl options={{ float: 'left' }} />
					<FullscreenControl options={{ float: 'right' }} />
					<SearchControl
						options={{ float: 'left' }}
						defaultOptions={{
							noPlacemark: true,
						}}
					/>
				</Map>
			</YMaps>
		</Box>
	);
};

export default MapPage;
